	import WucTab from '@/components/wuc-tab/wuc-tab.vue';
	import uniIcons from "@/components/uni-icons/uni-icons.vue"

	export default {
		data() {
			return {
				username: '',
				password: '',
				repeatPassword:'',
				tabList: [{
						name: '老师',
						icon: 'cuIcon-comment'
					},
					{
						name: '学生',
						icon: 'cuIcon-dianhua'
					},
					{
						name: '家长',
						icon: 'cuIcon-wifi'
					},
					{
						name: '管理员',
						icon: 'cuIcon-wifi'
					}
				],
				userType: 0,
				school:''
			};
		},
		components: {
			WucTab,
			uniIcons,
		},
		computed: {},
		onLoad() {
			this.school = uni.getStorageSync('school')
		},
		methods: {
			returnLogin() {
				uni.switchTab({
					url: '/pages/login/login'
				});
			},
			
			tabChange(index) {
				this.userType = index;
			},
			// 注册
			signUp() {
				const {
					username,
					password,
					repeatPassword,
					userType,
				} = this
				if(repeatPassword !== password){
					uni.showModal({
						content: '两次密码不一致',
						showCancel: false
					})
					return 
				}
				// 老师注册必有ls字段
				if(username.indexOf('ls') && this.userType == 0){
					uni.showModal({
						content: '老师的注册方式请咨询管理员',
						showCancel: false
					})
					return
				}
				// 管理员注册必有admin字段
				if(username.indexOf('admin')  && this.userType == 3){
					uni.showModal({
						content: '您不是管理，请离开',
						showCancel: false
					})
					return 
				}
				console.log(userType);
				uni.showLoading({
					title: '注册中...'
				})
				
				uniCloud.callFunction({
					name: 'signUp',
					data: {
						username,
						password,
						userType,
					},
				}).then((res) => {
					uni.hideLoading()
					if (res.result.status !== 0) {
						return Promise.reject(new Error(res.result.msg))
					}
					uni.setStorageSync('token', res.result.token)
                    // 显示消息提示框
                    uni.showToast({
                        icon:"none",
                        title:"注册成功，请重新登录",
                        success() {
							// 解决不跳转的bug
							uni.switchTab({
								url: '/pages/login/login'
							})
                        }
                    })
				}).catch((err) => {
					console.log(err);
					uni.hideLoading()
					uni.showModal({
						content: '注册失败，' + err.message,
						showCancel: false
					})
				})
			},
			validateToken() {
				uni.showLoading({
					title: '加载中...'
				});
				uniCloud.callFunction({
					name: 'validateToken',
					data: {
						token: uni.getStorageSync('token')
					}
				}).then((res) => {
					console.log(res);
					uni.hideLoading()
					uni.showModal({
						content: res.result.msg,
						showCancel: false
					})
				}).catch((err) => {
					uni.hideLoading()
					uni.showModal({
						content: '请求云函数发生错误，' + err.message,
						showCancel: false
					})
				})
			},
		},
		onReady() {}
	};
